import React ,{Component} from 'react';
import ContentRow1 from './Content-Row-section1/ContentRow1.js';
import ContentRow2 from './Content-Row-section2/ContentRow2.js';
import imagen from './product_dummy.svg'



class PageContent extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        error: null,
        isLoaded: false,
        amount:[],
        users:[]
      };
    }
  
    componentDidMount() {
        fetch("http://localhost:3000/apis/countProducts")
          .then(res => res.json())
          .then(
            (result) => {
                
              this.setState({
                isLoaded: true,
                amount: result.qtyProducts
                
              });
              
            },
            // Nota: es importante manejar errores aquí y no en 
            // un bloque catch() para que no interceptemos errores
            // de errores reales en los componentes.
            (error) => {
              this.setState({
                isLoaded: true,
                error
              });
            }
          )
          fetch("http://localhost:3000/apis/countUsers")
          .then(res => res.json())
          .then(
            (result) => {
                console.log(result)
              this.setState({
                
                users: result.qtyUsers
                
              });
              
            },
            // Nota: es importante manejar errores aquí y no en 
            // un bloque catch() para que no interceptemos errores
            // de errores reales en los componentes.
            (error) => {
              this.setState({
                isLoaded: true,
                error
              });
            }
          )
        
    }
 
  
    render() {
        const { error, isLoaded, amount , users } = this.state;
        
        return (
        <div className="container-fluid">

            <div className="d-sm-flex align-items-center justify-content-between mb-4">
					    <h1 className="h3 mb-0 text-gray-800">App Dashboard</h1>
			      </div>

            <div className="row">
                <ContentRow1 
                    tittle=" Products in Data Base"
                    amount= {amount}
                    icon="fas fa-clipboard-list fa-2x text-gray-300"
                />
                <ContentRow1 
                    tittle=" Amount in products"
                    amount="$546.456"
                    icon="fas fa-dollar-sign fa-2x text-gray-300"
                />
                <ContentRow1 
                    tittle=" Users quantity"
                    amount= {users}
                    icon="fas fa-user-check fa-2x text-gray-300"
                />

            </div>

            <div className="row">
						
                <div className="col-lg-6 mb-4">
                    <div className="card shadow mb-4">
                        <div className="card-header py-3">
                            <h6 className="m-0 font-weight-bold text-primary">Last product in Data Dase</h6>
                        </div>
                        <div className="card-body">
                            <div className="text-center">
                                <img className="img-fluid px-3 px-sm-4 mt-3 mb-4" style={{ width: '25rem' }} src={imagen} alt="image dummy" />
                            </div>
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolores, consequatur explicabo officia inventore libero veritatis iure voluptate reiciendis a magnam, vitae, aperiam voluptatum non corporis quae dolorem culpa exercitationem ratione?</p>
                            <a target="_blank" rel="nofollow" href="/">View product detail</a>
                        </div>
                    </div>
                </div>

                
                <div className="col-lg-6 mb-4">						
                    <div className="card shadow mb-4">
                        <div className="card-header py-3">
                            <h6 className="m-0 font-weight-bold text-primary">Categories in Data Base</h6>
                        </div>
                        <div className="card-body">
                            <div className="row">
                                <ContentRow2 
                                    category="Category 01"
                                />
                                <ContentRow2 
                                    category="Category 02"
                                />
                                <ContentRow2 
                                    category="Category 03"
                                />
                                <ContentRow2 
                                    category="Category 04"
                                />
                                <ContentRow2 
                                    category="Category 05"
                                />
                                <ContentRow2 
                                    category="Category 06"
                                />

                            </div>
                        </div>
                    </div>
                </div>
            </div>


        </div>
      );
    }
  }
  export default PageContent;